import React, { Component } from "react";
import { DocumentIcon, PlusCircleIcon } from "@heroicons/react/outline";
import _ from "lodash";

import { connect } from "react-redux";
import { AppBackButton, ClaimStatus } from "../components/AppComponents";
import { withRouter } from "react-router";
import Main from "./Main";
import Button from "../components/Button";
import { InputRadio } from "../components/form/InputRadio";
import {
  ApiGet,
  ApiPost,
  AppLogout,
  removeData,
  setData,
  setError,
  setMessage,
} from "../lib/AppHelper";
import PaginationData from "./admin/components/PaginationData";

import { Link } from "react-router-dom";
import { AppStore } from "../lib/AppStore";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEdit, faPencilAlt, faPhoneSquare, faRupeeSign } from "@fortawesome/free-solid-svg-icons";

export class Claims extends Component {
  BasicBtn = React.createRef();

  state = {
    claims: [],
  };

  render() {
    const { claims } = this.state;

    return (
      <Main MessageRef={this.MessageRef}>
        <div className="w-full flex md:flex-row flex-col">
          <div className="w-96">
            <AppBackButton to="/">Back to Dashboard</AppBackButton>
          </div>
        </div>

        <div className="w-full flex flex-col justify-between space-y-4">
          <div>
            <div className="container my-5 mx-auto px-1">
              <div className="flex flex-wrap content-center">
                <PaginationData
                  data={claims}
                  request={ApiGet}
                  renderItem={(item) => <ClaimCard item={item} />}
                  endPoint={"claims"}
                />
              </div>
            </div>
          </div>
        </div>
      </Main>
    );
  }

  componentDidMount() {
    this.getClaims();
  }
  getClaims = async () => {
    AppStore.dispatch({ type: "LOADING", loading: true });

    const response = await ApiGet("claims");

    if (response.status === "success") {
      this.setState({ claims: response.data });
    } else {
      this.MessageRef.current.show(response.message);
    }

    AppStore.dispatch({ type: "LOADING", loading: false });
  };
}

const mapStateToProps = (state) => {
  const { loading } = state;
  return {
    loading: loading,
  };
};

const ClaimsConnect = connect(mapStateToProps)((props) => {
  return <Claims {...props} />;
});

export default withRouter(ClaimsConnect);

export const ClaimCard = ({ item }) => {
  return (
    <div className="mb-5 p-2" style={{ width: 400 }} >
      <article className="rounded-lg shadow-lg border-red">
        <header className="flex items-center justify-between leading-tight p-2 md:p-4">
          <h1 className="text-lg  text-black font-bold text-xl capitalize mr-4 pl-1">
            {item.member && item.member.name ? item.member.name : "Name empty"}
          </h1>
          <p className="text-grey-darker text-sm">
            <ClaimStatus status={item.status} />
            {item.status == 'DRAFT' ?
              <FontAwesomeIcon icon={faEdit} /> : false}

          </p>
        </header>
        <body className="text-sm items-center justify-between leading-tight p-4">
          <p className="pt-2">{item.claim_reason}</p>
          <p className="pt-2">
            {item.hopital ? item.hopital.name : "Hospital name not available"}
          </p>

          <p className="pt-2 ">
            Total claimed Amount:
            <span className="pl-2">   <FontAwesomeIcon icon={faRupeeSign} />{item.total_claim_amount}</span>
          </p>
        </body>
        <footer className="pb-4 my-2 justify-between text-right ">
          <Link to={"/claims/" + item.claim_id}>
            <button class="text-xs mr-3 bg-transparent hover:bg-red-300  font-semibold hover:text-white py-2 px-4 border border-red-300 hover:border-transparent rounded">
              View Claim
            </button>
          </Link>
        </footer>
      </article>
    </div>
  );
};
