import _ from "lodash";
import React, { useRef, Component } from "react";
import { ButtonGroup, Input, InputGroup, SelectPicker, Button } from "rsuite";
import SearchIcon from "@rsuite/icons/Search";
import { Form, Schema, AutoComplete } from "rsuite";
import InputPicker from "rsuite/InputPicker";
import { ApiGet } from "../lib/AppHelper";
import { AppStore } from "../lib/AppStore";

const TextField = ({ name, label, accepter, ...rest }) => (
  <Form.Group controlId={name}>
    <Form.ControlLabel>{label} </Form.ControlLabel>
    <Form.Control name={name} accepter={accepter} {...rest} />
  </Form.Group>
);

const Field = React.forwardRef((props, ref) => {
  const { name, message, label, accepter, error, style, ...rest } = props;
  return (
    <Form.Group
      controlId={`${name}-10`}
      ref={ref}
      className={error ? "has-error" : ""}
      style={{ ...style }}
    >
      <Form.Control
        name={name}
        accepter={accepter}
        errorMessage={error}
        {...rest}
      />
    </Form.Group>
  );
});

export class Search extends Component {
  formRef = React.createRef();

  state = {
    formError: {},
    formValue: {},
    checkTrigger: "change",
    model: Schema.Model({
      search_type: Schema.Types.StringType().isRequired(
        "This field is required."
      ),
      search: Schema.Types.StringType().isRequired("This field is required."),
    }),
    params: {},
    errors: {},
    option: {},
    handleSubmit: "",
    members: [],
    options: [
      {
        name: "Member ID",
        value: "member_id",
      },
      {
        name: "Member Name",
        value: "name",
      },
      {
        name: "Mobile Number",
        value: "mobile",
      },
    ],
  };

  // searchMember = async (a, b) => {
  //   const response = await ApiGet("search-member", { search: a });

  //   if (response.status == "success") {
  //     console.log("@@@=", response.data[0].name);

  //     this.setState({ members: response.data });
  //   }
  //   AppStore.dispatch({ type: "LOADING", loading: false });
  // };

  handleSubmit = (value) => {
    if (!this.formRef.current.check()) {
      console.error("Form Error");
      return;
    }
    const { setValue } = this.props;
    setValue && setValue(this.state.formValue);
    console.log(this.formRef.formValue);
  };

  setFormValue = (e) => {
    this.setState({ formValue: e });
  };
  setFormError = (e) => {
    this.setState({ formError: e });
  };

  render() {
    const {
      options,
      model,
      formValue,
      data,
      formError,
      checkTrigger,
      members,
    } = this.state;
    console.log("@@@@membersmembersmembers:::", members);
    return (
      <div className="search">
        <Form
          formValue={formValue}
          ref={this.formRef}
          onChange={this.setFormValue}
          onCheck={this.setFormError}
          formError={formError}
          formDefaultValue={formValue}
          model={model}
          checkTrigger={checkTrigger}
        >
          <div
            className="border"
            style={{ display: "flex", flexDirection: "row" }}
          >
            <Field
              name="search_type"
              searchable={false}
              defaultValue={this.state.option.name}
              cleanable={false}
              data={options}
              accepter={SelectPicker}
              labelKey="name"
              valueKey="value"
              onSelect={(_v, item) => {
                this.setState({ option: item });
              }}
            />
            <Field
              autoComplete="off"
              name="search"
              accepter={AutoComplete}
              error={formError.search}
              style={{
                width: "90%",
              }}
              data={members}
              placeholder="Search Member..."
              // onChange={this.searchMember}
            />

            <InputGroup.Button
              style={{ height: 41 }}
              type="submit"
              onClick={this.handleSubmit}
            >
              <SearchIcon />
            </InputGroup.Button>
          </div>
        </Form>

        {/* <InputGroup > */}
        {/* <AutoComplete data={data} onChange={this.getMember} /> */}
        {/* <AutoComplete name='search' error={formError.search} data={data} onChange={() => { */}
        {/* this.getMember(data, '2222'); */}
        {/* }} /> */}
        {/* <TextField name="search" placeholder="Search..." /> */}
        {/* </InputGroup> */}

        {/* <input type="email" name="email" id="email" placeholder="Search....." className="form-input w-full" /> */}
      </div>
    );
  }
}

const styles = {
  marginBottom: 20,
  flexDirection: "row",
};
