import React from 'react';
import { Provider, useSelector } from 'react-redux'
import { BrowserRouter as Router, Switch, Route, Redirect } from "react-router-dom";
import { AppStore } from './lib/AppStore';

import Login from './screens/Login';
import PrivateRoute from './lib/PrivateRoute';
import { ApiGet, getData, removeData, setError } from './lib/AppHelper';
import AppRoutes from './lib/AppRoutes';

const LoginRoute = () => {
  const token = useSelector(state => state.token)
  if (token) return <Redirect to={{ pathname: "/" }} />
  return <Login />
}

function App() {

  const [loaded, setLoaded] = React.useState(false)
  // const [timeLoaded, setTimeLoaded] = React.useState(false)

  React.useEffect(() => {

    async function bootstrap() {
      const token = await getData('access_token');
      const is_admin = await getData('is_admin');

      if (token) {
        const response = await ApiGet(`${is_admin ? 'admin/' : ''}me`);
        if (response.status === 'success') {
          AppStore.dispatch({ type: 'SIGN_IN', user: response.data, token: token })
        } else {
          await removeData('access_token')
          await setError(response.message)
        }
        setLoaded(true);
      } else {
        setLoaded(true);
      }

      /* setTimeout(() => {
        setTimeLoaded(true)
      }, 2000) */
    }

    bootstrap();

    return () => {
    }
  }, [])

  if (!loaded) return false

  /*
  <React.Fragment>
    <div className="fixed w-screen flex items-center justify-center h-screen bg-gradient-to-r from-primary-600 to-primary-500 z-50">
      <div className="w-96">
        <AiqaIcon className="w-full text-white" />
      </div>
    </div>
  </React.Fragment>
  */

  return (
    <Provider store={AppStore}>
      <Router>
        <Switch>
          <Route path="/login">
            <LoginRoute />
          </Route>

          <PrivateRoute>
            <AppRoutes />
          </PrivateRoute>

          <Route path="*" render={() => {
            return <Redirect to="/login" />
          }} />
        </Switch>
      </Router>
    </Provider>
  );
}

export default App;
