import React, { Component } from 'react'
import { AppBackButton, AppLabel, AppStatusLabel, AppTeamStatus } from '../../components/AppComponents'
import { AppDataTable } from '../../components/AppDataTable'
import { AppDataElement } from '../../components/AppDataElement'
import Main from '../Main';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faUserFriends } from '@fortawesome/free-solid-svg-icons'
import { Link, Redirect, useHistory } from 'react-router-dom';
import Button from '../../components/Button';



export class Employees extends Component {

   DataTable = React.createRef();
   MessageRef = React.createRef();

   state = { type: "", team_count: 10, handleClick: '' }

   handleClick = () => {
      console.log('this is:', this);
   }




   render() {
      const { type } = this.state;
      return (
         <Main MessageRef={this.MessageRef}>
            <AppBackButton to="/">Back to Dashboard</AppBackButton>
            <Button
               onClick={this.handleClick}
            >
               Back
            </Button>

            <AppDataElement MessageRef={this.MessageRef} endpoint={'members'} header={[]} render={(item, onClick) => {
               return <>
                  <div className="max-w-sm rounded overflow-hidden shadow-lg">
                     <div className="px-6 py-4">
                        <div className="font-bold text-xl mb-1 capitalize">{item.name}
                           {/* <span className="ml-2 text-xs text-gray-400 ">Level</span> */}


                           {item.team_cnt > 0 ?
                              <Link to={"/employees/" + item.id}>
                                 <span onClick={() => onClick(item.id)} className="float-right text-sm text-primary">
                                    <FontAwesomeIcon icon={faUserFriends} /> {item.team_cnt}  </span></Link>
                              :

                              <span className="float-right text-sm text-primary">
                                 <FontAwesomeIcon icon={faUserFriends} /> {item.team_cnt}

                              </span>
                           }

                           {/* <span className="text-sm ml-2">{item.mobile}</span> */}
                        </div>
                     </div>
                     <div className="px-3 pt-4 pb-2">

                        <AppLabel title='CFO' color={'orange'} />
                        {/* <AppLabel title='Level' color={'yellow'} /> */}
                        <span className="float-right">
                           {item.status != 1 ?
                              <AppTeamStatus status={3} sent="Sent" inactive="Inactive" pending="Invitation Sent" waiting="Waiting for approval" />
                              : ''}
                        </span>

                     </div>
                  </div>

               </>
            }
            } params={{ type: type }} filters={<>
            </>} />

         </Main >
      )


   }


   componentWillReceiveProps(nextProps) {

      if (nextProps.location !== this.props.location) {
         this.setState({ prevPath: this.props.location })
      }
   }

}

export default Employees
