import React, { useCallback, useEffect, useState } from "react";
import "rsuite-table/lib/less/index.less"; // or 'rsuite-table/dist/css/rsuite-table.css'
import { Pagination } from "rsuite";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import moment from "moment";
import { Link } from "react-router-dom";
import { faPhoneSquare } from "@fortawesome/free-solid-svg-icons";
import Button from "../../../components/Button";
const calculatedHeight = window.innerHeight - 230; // 230 is by appHeader - 60, appfooter - 25, paginationContainer - 60, paddingTop-15, paddingBottom-15, buttons - 30
const initialRowHeight = 46;
const initialHeaderHeight = 40;
const calculatedLimit =
    (calculatedHeight - initialHeaderHeight) / initialRowHeight;
const PaginationData = ({
    request,
    data,
    limit = 15,
    pagination = true,
    filterValues,
    loading,
    endPoint,
    extraData,
    renderItem,
}) => {
    if (limit < 1) {
        limit = 1;
    }
    const [page, setPage] = useState(1);
    const [totalCount, setTotalCount] = useState(0);
    const [tableData, setTableData] = useState(data);
    const [listLoading, setLoading] = useState(false);

    useEffect(() => {
        const start = limit * (page - 1);
        if (!data) {
            (async () => {
                setLoading(true);
                try {
                    const res = await request(endPoint, extraData, {
                        start,
                        limit,
                        ...filterValues,
                    });
                    if (res.data.total) {
                        setTotalCount(res.data.total);
                    }
                    setTableData(res.data.data);
                } catch (err) {
                } finally {
                    setLoading(false);
                }
            })();
        } else {
            const newData = [...data];
            const update = newData.slice(start, limit * page);
            setTotalCount(data.length);
            setTableData(update);
        }
    }, [setTableData, setTotalCount, request, page, data, filterValues]);
    return (
        <div style={{ display: "flex", flexDirection: "column", flex: 1 }}>
            {tableData && tableData.length === 0 ? (
                listLoading ? (
                    <p>loading...</p>
                ) : (
                    <>
                        {/* <div className="text-center">
                        <p className="text-xl">You do not have any subscriptions</p>
                        <Link to="/subscribe"><Button title="Add New Member" className="max-w-min mx-auto mt-4" /></Link>
                    </div> */}
                    </>
                )
            ) : (
                <div className="flex flex-wrap" >
                    {tableData &&
                        tableData.map((item) => {
                            if (renderItem) {

                                return renderItem(item);
                            }
                            return (
                                <div className="my-1 px-1 w-full md:w-1/2 lg:my-4 lg:px-4 lg:w-1/3">
                                    <article className="overflow-hidden rounded-lg shadow-lg">
                                        <header className="flex items-center justify-between leading-tight p-2 md:p-4">
                                            <h1 className="text-lg">
                                                <Link
                                                    className="no-underline hover:underline text-black font-bold text-xl capitalize"
                                                    to={`/subscription/${item.application_id}`}
                                                >
                                                    {item.customer_name ? item.customer_name : item.name}
                                                </Link>
                                            </h1>
                                            <p className="text-grey-darker text-sm">
                                                {moment(item.start_date).format("DD, MMM YYYY")}
                                            </p>
                                        </header>
                                        <body className="flex items-center justify-between leading-tight p-2 md:p-4">
                                            <p className="text-sm text-clip overflow-hidden ...">
                                                {item.product_name}
                                            </p>
                                            <a
                                                class="flex items-center no-underline text-black"
                                                href={`tel:+91${item.mobile}`}
                                            >
                                                <p class="ml-2 text-sm">
                                                    <FontAwesomeIcon
                                                        icon={faPhoneSquare}
                                                        className="text-primary"
                                                    />{" "}
                                                    {item.mobile}
                                                </p>
                                            </a>
                                        </body>
                                    </article>
                                </div>
                            );
                        })}
                </div>
            )}
            {pagination ? (
                <div style={{ padding: 20 }}>
                    <Pagination
                        prev
                        next
                        first
                        last
                        ellipsis
                        boundaryLinks
                        maxButtons={2}
                        size="xs"
                        layout={["total", "-", "pager", "skip"]}
                        total={totalCount}
                        //   limitOptions={[10, 20]}
                        limit={limit}
                        activePage={page}
                        onChangePage={setPage}
                    //   onChangeLimit={handleChangeLimit}
                    />
                </div>
            ) : null}
        </div>
    );
};

export default PaginationData;
