import React from "react";
import { DatePicker } from "rsuite";
import InputLabel from "../../components/form/InputLabel";

export const DateInput = ({ label, error, setValue, value }) => {
  let newValue = value;
  if (newValue) {
    let dateFormatter = newValue.split("-");
    newValue = new Date(
      `${dateFormatter[1]}-${dateFormatter[0]}-${dateFormatter[2]}`
    );
  }
  return (
    <div className="mb-5">
      <InputLabel label={label} />
      <div className="mt-2 relative">
        <DatePicker
          style={{ width: "100%" }}
          autoSave
          format="yyyy-MM-dd"
          className="genderClass padding0"
          oneTap
          value={newValue || void 0}
          onChange={setValue}
        />
        {error ? <p className="mt-2 text-xs text-red-500">{error}</p> : false}
      </div>
    </div>
  );
};
