export const AppLabel = ({ title, color = "primary" }) => {
  let text_color = "text-primary-800";
  let bg_color = "bg-primary-100";

  switch (color) {
    case "blue":
      text_color = "text-blue-800";
      bg_color = "bg-blue-100";
      break;
    case "indigo":
      text_color = "text-indigo-800";
      bg_color = "bg-indigo-100";
      break;
    case "purple":
      text_color = "text-purple-800";
      bg_color = "bg-purple-100";
      break;
    case "pink":
      text_color = "text-pink-800";
      bg_color = "bg-pink-100";
      break;
    case "orange":
      text_color = "text-orange-800";
      bg_color = "bg-orange-100";
      break;
    case "teal":
      text_color = "text-teal-800";
      bg_color = "bg-teal-100";
      break;
    default:
      text_color = `text-${color}-800`;
      bg_color = `bg-${color}-100`;
      break;
  }

  return (
    <span
      className={`px-3 inline-flex text-xs leading-5 font-semibold mr-2 rounded-full ${text_color} ${bg_color}`}
    >
      {title}
    </span>
  );
};

export const MemberIcon = ({ member, size = 24 }) => {
  if (member.relation === "self" && member.gender === "m")
    return <MaleIcon size={size} />;

  if (member.relation === "self" && member.gender === "f")
    return <FemaleIcon size={size} />;

  if (member.relation === "spouse") return <RingIcon size={size} />;

  if (member.relation === "daughter") return <DaughterIcon size={size} />;

  if (member.relation === "son") return <SonIcon size={size} />;

  if (member.relation === "parent" || member.relation === "spouse_parent")
    return <ParentsIcon size={size} />;

  return false;
};
export const MemberIconWithName = ({
  member,
  size = 30,
  setExpanded,
  expanded,
  policyId,
}) => {
  const getHeader = () => {
    if (member.relation === "SELF" && member.gender === "MALE")
      return (
        <div>
          <MaleIcon size={size} className="absolute" />
          <p className="pl-10 text-medium text-md">{member.name}</p>
          <p className="pl-10 text-xs">{member.relation}</p>
        </div>
      );

    if (member.relation === "SELF" && member.gender === "FEMALE")
      return (
        <div>
          {" "}
          <FemaleIcon className="absolute" size={size}>
            {member.name}
          </FemaleIcon>
          <p className="pl-10 text-medium text-md">{member.name}</p>
          <p className="pl-10 text-xs">{member.relation}</p>
        </div>
      );

    if (member.relation === "SPOUSE")
      return (
        <div>
          {" "}
          <RingIcon size={size} className="absolute"></RingIcon>
          <p className="pl-10 text-medium text-md">{member.name}</p>
          <p className="pl-10 text-xs">{member.relation}</p>
        </div>
      );

    if (member.relation === "DAUGHTER")
      return (
        <div>
          {" "}
          <DaughterIcon size={size} className="absolute"></DaughterIcon>
          <p className="pl-10 text-medium text-md">{member.name}</p>
          <p className="pl-10 text-xs">{member.relation}</p>
        </div>
      );

    if (member.relation === "SON")
      return (
        <div>
          {" "}
          <SonIcon size={size} className="absolute"></SonIcon>
          <p className="pl-10 text-medium text-md">{member.name}</p>
          <p className="pl-10 text-xs">{member.relation}</p>
        </div>
      );

    if (member.relation === "PARENT" || member.relation === "spouse_parent")
      return (
        <div>
          {" "}
          <ParentsIcon size={size} className="absolute"></ParentsIcon>
          <p className="pl-10 text-medium text-md">{member.name}</p>
          <p className="pl-10 text-xs">{member.relation}</p>
        </div>
      );
  };
  return (
    <div
      onClick={() => {
        setExpanded((expanded) => !expanded);
      }}
      style={{
        flexDirection: "row",
        display: "flex",
        justifyContent: "space-between",
      }}
    >
      {getHeader()}
      <div className="flex justify-center" style={{ alignItems: "center" }}>
        <Link
          to={{
            pathname: `/claim/${member.id}/${policyId}`,
            params: { memberId: member.id, policyId },
          }}
        >
          <button class="text-xs mr-3 bg-transparent hover:bg-red-300  font-semibold hover:text-white py-2 px-4 border border-red-300 hover:border-transparent rounded">
            Raise Claim
          </button>
        </Link>
        <div style={{ width: 10 }}>{expanded ? "-" : "+"}</div>
      </div>
    </div>
  );
};

export const Tenor = ({ tenor }) => {
  if (tenor == 18) return "1.5 Years";
  else if (tenor == 1) return "1.5 Year";
  else if (tenor == 2) return "2 Years ";
  else return tenor + " Month" + tenor > 0 ? "S" : "";

  return false;
};
const convertBase64ToFile = (base64String, fileName, type) => {
  let bstr = atob(base64String);
  let n = bstr.length;
  let uint8Array = new Uint8Array(n);
  while (n--) {
    uint8Array[n] = bstr.charCodeAt(n);
  }
  let file = new File([uint8Array], fileName, { type });
  return file;
};
const downloadBase64Data = (base64String, fileName, type) => {
  try {
    let file = convertBase64ToFile(base64String, fileName, type);
    saveAs(file, fileName);
  } catch (e) {
    console.log("@@@@@@file", e);
  }
};

export const DocumentView = ({ item }) => {
  const getData = async () => {
    getApiClaim(`docs/${item.id}`)
      .then((res) => {
        downloadBase64Data(
          res.data.encodedResource,
          res.data.document.documentName,
          res.data.mimeType
        );
      })
      .catch(() => {});
  };

  return (
    <div class="max-w-sm rounded overflow-hidden shadow-md border-dashed border-2 border-red-200 ">
      <div class="px-6 py-4">
        <span className="text-xl">
          <FontAwesomeIcon icon={faImage} className=""></FontAwesomeIcon>
        </span>
        <div class="mb-2 pt-2 text-gray-500 text-base">{item.documentName}</div>
        <div
          className="text-xl"
          style={{ cursor: "pointer" }}
          onClick={getData}
        >
          <FontAwesomeIcon icon={faEye}></FontAwesomeIcon>
        </div>
      </div>
    </div>
  );
};

export const ClaimStatus = ({ status }) => {
  let text_color = "text-primary-800";
  let bg_color = "bg-primary-100";

  switch (status) {
    case "NEW":
      text_color = "text-blue-800";
      bg_color = "bg-blue-100";
      break;
    case "REJECT":
      text_color = "text-red-800";
      bg_color = "bg-red-100";
      break;
    case "DRAFT":
      text_color = "text-yellow-800";
      bg_color = "bg-yellow-100";
      break;
    default:
      text_color = `text-primary-800`;
      bg_color = `bg-primary-100`;
      break;
  }

  return (
    <span
      className={`px-3 inline-flex text-xs leading-5 font-semibold mr-2 rounded-full ${text_color} ${bg_color}`}
    >
      {status}
    </span>
  );
};
