import React, { Component } from 'react'
import { AppHeading } from '../../components/AppComponents'
import { InputGroup } from '../../components/form/InputGroup'
import Countdown from 'react-countdown';
import moment from 'moment'
import _ from 'lodash'
import { DuplicateIcon } from '@heroicons/react/outline'
import paymentImg from './../../payment-img.png'
import ClipboardJS from 'clipboard'
import { Fragment } from 'react'
import { Transition } from '@headlessui/react'
import { connect } from 'react-redux';
import { FaWhatsapp } from 'react-icons/fa';
import { FiSend } from 'react-icons/fi';
import { AppStore } from '../../lib/AppStore';
import { ApiGet, ApiPost } from '../../lib/AppHelper';

export class Payment extends Component {

   CopyLinkBtn = React.createRef();

   state = {
      errors: {},
      copied: false
   }

   checkInterval = null;

   render() {
      const { errors, copied } = this.state;
      const { application } = this.props;

      return (
         <div className="w-full px-6 max-w-md md:flex-grow-0 flex-1 flex flex-col justify-between space-y-6">
            <div>
               <AppHeading>Payment</AppHeading>
               <h4 className="mt-1 text-md mb-6 mt-6">Payment link is sent to the registered mobile number. +91-{application.mobile}</h4>
               <InputGroup error={errors.name} label="Payment link" placeholder="" attrs={{ value: application.payment_link.replace(/(^\w+:|^)\/\//, ''), readOnly: true }} action={<div tabIndex={0} data-clipboard-text={application.payment_link} ref={this.CopyLinkBtn} className="focus:ring-2 focus:ring-primary focus:ring-offset-2 focus:outline-none rounded-xl cursor-pointer text-primary shadow-sm border-primary focus:bg-primary-50 px-3 flex items-center justify-center border-gray-300 rounded-xl border ml-3 shadow-sm position-relative">
                  <Transition
                     as={Fragment}
                     show={copied}
                     enter="transform transition duration-75"
                     enterFrom="opacity-0 scale-50"
                     enterTo="opacity-100 scale-100"
                     leave="transform duration-75 transition"
                     leaveFrom="opacity-100 scale-100 "
                     leaveTo="opacity-0 scale-50"
                  >
                     <span className="bg-gray-700 text-white px-2 py-1 rounded-md text-xs transition-opacity" style={{ position: 'absolute', top: -30 }}>Copied!</span>
                  </Transition>
                  <DuplicateIcon width={20} />
               </div>} />
               <div className="flex space-x-3">
                  <button onClick={this.sendSMS} title="Send SMS" className="w-12 h-12 rounded-full bg-gray-600 flex justify-center items-center focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary-500">
                     <FiSend size={20} className="text-white" />
                  </button>
                  <button onClick={this.sendWhatsapp} title="Send On Whatsapp" className="w-12 h-12 rounded-full bg-green-500 flex justify-center items-center focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary-500">
                     <FaWhatsapp size={24} className="text-white" />
                  </button>
               </div>
            </div>
            <div className="flex mt-10 flex-col items-center">
               <img alt="" className="w-40 h-40 rounded-full" src={paymentImg} />
               <p className="text-gray-400 text-sm mb-2 mt-3">PAGE EXPIRE IN</p>
               <p className="text-xl font-semibold"><Countdown onComplete={this.onCompleteCountdown} renderer={this.countdownRenderer} date={moment().add(5, 'minutes').toDate()} /></p>
            </div>
         </div>
      )
   }

   componentDidMount() {
      const _this = this;
      const clipboard = new ClipboardJS(this.CopyLinkBtn.current);
      clipboard.on('success', () => {
         _this.setState({ copied: true }, () => {
            setTimeout(() => {
               _this.setState({ copied: false })
            }, 2000)
         });
      })

      this.checkInterval = setInterval(this.check, 5000);
   }

   componentWillUnmount() {
      clearInterval(this.checkInterval);
   }

   onCompleteCountdown = () => {
      this.props.onError();
   }

   countdownRenderer = ({ minutes, seconds }) => {
      return `${_.padStart(minutes, 2, '0')}:${_.padStart(seconds, 2, '0')}`;
   }

   sendSMS = async () => {
      AppStore.dispatch({ type: 'LOADING', loading: true })

      const response = await ApiPost('payment/send-link', { id: this.props.application.id });

      if (response.status === 'success') {
         this.props.MessageRef.current.show(response.data.message, 'success');
      } else {
         this.props.MessageRef.current.show(response.message, 'error');
      }

      AppStore.dispatch({ type: 'LOADING', loading: false })
   }

   sendWhatsapp = () => {
      const { application } = this.props;

      let url = `https://api.whatsapp.com/send?text=` + encodeURIComponent(`Dear ${application.name}, Doctor On Call payment URL ${application.payment_link} to activate your product.`);

      window.open(url)
   }

   check = async () => {
      const response = await ApiGet(`payment/check/${this.props.application.id}`);
      if (response.status === 'success') {
         if (response.data.status === true) {
            this.props.onSuccess(response.data.message)
         }
      } else {
         this.props.onError(response.message)
      }
   }
}

const mapStateToProps = (state) => {
   const { loading } = state
   return { loading: loading }
};

export default connect(mapStateToProps)(props => {
   return <Payment {...props} />
})