import React, { Component } from 'react'
import { Disclosure } from '@headlessui/react'
import { ChevronRightIcon } from '@heroicons/react/outline'
import _ from 'lodash';
import { DaughterIcon, FemaleIcon, MaleIcon, ParentsIcon, RingIcon, SonIcon } from '../../components/AppIcons';
import { InputGroup } from '../../components/form/InputGroup';
import { InputRadio } from '../../components/form/InputRadio';

export class FamilyDetails extends Component {

   state = {
      params: null
   }

   render() {
      const members = this.makeForm();
      const { params } = this.state;

      if (params === null) return false

      return (
         <div className="w-full">
            {members.map((member) => {
               return <Disclosure defaultOpen={member.key === 'self'} key={member.key}>
                  {({ open }) => (
                     <>
                        <Disclosure.Button className="flex justify-between w-full px-4 py-5 border-b border-gray-100 border-dashed last:border-b-0 font-medium text-left focus:outline-none focus:ring-2 focus:ring-primary-200 focus:ring-offset-2">
                           <div className="flex">
                              <span>{member.icon}</span>
                              <span className="ml-2">{member.title}</span>
                           </div>
                           <ChevronRightIcon className={`${open ? 'transform rotate-90' : ''} p-1 rounded-full w-6 h-6 text-primary shadow`}
                           />
                        </Disclosure.Button>
                        <Disclosure.Panel className="px-4 pt-4 pb-2 text-sm">
                           {member.relation === 'self' ? false : <InputGroup label="Name" attrs={{ value: params[member.key].name, onChange: (e) => this.onChangeHandler(e, `${member.key}.name`) }} />}

                           {member.relation === 'self' ? false : <InputGroup label="Date of Birth" attrs={{ type: 'date', value: params[member.key].dob, onChange: (e) => this.onChangeHandler(e, `${member.key}.dob`) }} />}

                           {_.indexOf(['self', 'daughter', 'son'], member.relation) === -1 ? <InputRadio label="Gender" name={`${member.key}.gender`} options={[{ label: 'Male', value: 'm' }, { label: 'Female', value: 'f' }]} value={params[member.key].gender} attrs={{ onChange: (e) => this.onChangeHandler(e, `${member.key}.gender`) }} /> : false}

                           <InputGroup label="Height (cms)" attrs={{ min: 0, type: 'number', value: params[member.key].height, onChange: (e) => this.onChangeHandler(e, `${member.key}.height`) }} />

                           <InputGroup label="Weight (kgs)" attrs={{ type: 'number', min: 0, value: params[member.key].weight, onChange: (e) => this.onChangeHandler(e, `${member.key}.weight`) }} />
                        </Disclosure.Panel>
                     </>
                  )}
               </Disclosure>
            })}
         </div>
      )
   }

   componentDidMount() {
      const members = this.makeForm();

      let value = this.props.value.members_detail;
      _.forEach(members, (member) => {
         if (member.relation !== 'self') {
            _.set(value, `${member.key}.name`, _.get(value, `${member.key}.name`, ``));
            _.set(value, `${member.key}.dob`, _.get(value, `${member.key}.dob`, ``));
         }
         if (_.indexOf(['self', 'daughter', 'son'], member.relation) === -1) {
            _.set(value, `${member.key}.gender`, _.get(value, `${member.key}.gender`, ``));
         }
         _.set(value, `${member.key}.height`, _.get(value, `${member.key}.height`, 0));
         _.set(value, `${member.key}.weight`, _.get(value, `${member.key}.weight`, 0));
      })

      this.setState({ params: value }, () => {
         this.props.onChange(value)
      })
   }

   makeForm = () => {
      const { value } = this.props

      const { spouse, daughter, son, parents } = value.members;
      const count = value.members_count;

      const members = [];

      members.push({
         key: 'self',
         relation: 'self',
         title: `Policy Holder (${value.name})`,
         icon: value.gender === 'm' ? <MaleIcon size={24} /> : <FemaleIcon size={24} />
      })

      if (spouse) {
         members.push({
            key: 'spouse',
            relation: 'spouse',
            title: 'Spouse',
            icon: <RingIcon size={24} />
         })
      }

      if (daughter) {
         for (let i = 1; i <= count.daughter; i++) {
            members.push({
               key: `daughter_${i}`,
               relation: 'daughter',
               title: count.daughter > 1 ? `Daughter ${i}` : `Daughter`,
               icon: <DaughterIcon size={24} />
            })
         }
      }

      if (son) {
         for (let i = 1; i <= count.son; i++) {
            members.push({
               key: `son_${i}`,
               relation: 'son',
               title: count.son > 1 ? `Son ${i}` : `Son`,
               icon: <SonIcon size={24} />
            })
         }
      }

      if (parents) {
         for (let i = 1; i <= count.parents.self; i++) {
            members.push({
               key: `self_parent_${i}`,
               relation: 'parent',
               title: count.parents.self > 1 ? `Parent ${i} (Self)` : `Parent (Self)`,
               icon: <ParentsIcon size={24} />
            })
         }

         for (let i = 1; i <= count.parents.spouse; i++) {
            members.push({
               key: `spouse_parent_${i}`,
               relation: 'spouse_parent',
               title: count.parents.spouse > 1 ? `Parent ${i} (Spouse)` : `Parent (Spouse)`,
               icon: <ParentsIcon />
            })
         }
      }

      return members;
   }

   onChangeHandler = (e, key) => {
      const { params } = this.state;
      _.set(params, key, e.target.value);
      this.setState({ params: params }, () => {
         this.props.onChange(this.state.params)
      })
   }
}

export default FamilyDetails

