import { UploadIcon } from '@heroicons/react/outline';
import React, { Component, useState } from 'react'
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { AppBackButton, AppHeading, AppRow, MemberIcon, MemberIconWithName, Tenor } from '../components/AppComponents';
import Button from '../components/Button';
import { ApiGet, setError } from '../lib/AppHelper';
import { AppStore } from '../lib/AppStore';
import Payment from './application/Payment';
import VpaMandate from './application/VpaMandate';
import Main from './Main'
import { Panel, PanelGroup } from 'rsuite';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faRupeeSign, faUsers, faCalendar } from '@fortawesome/free-solid-svg-icons';
import * as moment from 'moment'


const ExpandableComponent = ({ member, policyId }) => {
   const [expanded, setExpanded] = useState(false)
   return <Panel key={member.id} header={<MemberIconWithName member={member} setExpanded={setExpanded} expanded={expanded} policyId={policyId} />} expanded={expanded} id={'panel' + member.id} defaultExpanded={false}>
      {expanded ? '' : null}
   </Panel>
}

export class Application extends Component {

   MessageRef = React.createRef();

   state = {
      application: null,
   }


   render() {
      const { application } = this.state;


      if (application === null) return false


      // const { loading } = this.props;

      return (

         <Main>
            <AppBackButton to="/">Back to Dashboard</AppBackButton>
            <div className="flex flex-col md:flex-row space-x-0 md:space-x-5 space-y-5 md:space-y-0 px-2 space-y-6 customer-detail">
               <div className="flex-auto">
                  {application.status === 0 ? <>
                     {application.product_type === "mutual_aid" ? <VpaMandate onSuccess={this.onSuccess} onError={this.onError} MessageRef={this.MessageRef} application={application} /> : <Payment onSuccess={this.onSuccess} onError={this.onError} MessageRef={this.MessageRef} application={application} />}
                  </> : <>
                     <AppHeading>{application.customer_name}</AppHeading>
                     <h4 className="text-md mb-2 mt-6 flex items-center space-x-2">

                        {/* <p>{application.address.join(', ')}</p> */}
                        <span className="w-2 h-2 bg-primary rounded-full"></span>
                        {/* <p>Member since {application.member_since}</p> */}
                        <p>Member since {moment(application.start_date).format('DD MMMM YYYY')}
                        </p>
                        <span className="w-2 h-2 bg-primary rounded-full"></span>
                        <p>Valid till {moment(application.end_date).format('DD MMMM YYYY')}</p>
                     </h4>
                     <h4 className="text-md mb-6 mt-2 flex items-center space-x-2">

                        <p><a href={"mailto:" + application.email}>{application.email},</a></p>
                        <p><a href={"tel:" + application.mobile}>{application.mobile}</a></p>
                     </h4>

                     <div className="mb-8 grid grid-rows-2 md:grid-rows-1 items-center text-xs md:text-sm  grid-flow-col gap-1">
                        <div className="bg-red-100 rounded-md p-2 mr-4 mt-2">
                           <FontAwesomeIcon icon={faRupeeSign} /> 1 Lac Max Claim Amount
                        </div>
                        <div className=" bg-red-100 rounded-md p-2  mr-4 mt-2">
                           <FontAwesomeIcon icon={faUsers} />  4-9 Members
                        </div>

                        <div className="bg-red-100 rounded-md p-2  mr-4 mt-2">
                           <FontAwesomeIcon icon={faCalendar} />  <Tenor tenor={application.tenor} />
                        </div>
                        <div className="bg-red-100 rounded-md p-2 mt-2">
                           <FontAwesomeIcon icon={faRupeeSign} />  1,000/- Max Contribution
                        </div>
                     </div>

                     <AppHeading className="mb-4 pt-4">Your medical card</AppHeading>


                     <div className="bg-primary flip-front p-4 text-white rounded-lg h-48 w-96 max-w-full flex flex-col justify-between">

                        <p className="text-lg">{application.product_name}</p>
                        {/* <p className="font-thin">{application.member_since.length} family members</p> */}

                        <div className="flex space-x-20">
                           {/* <div>
                              <FontAwesomeIcon icon={faRupeeSign} />
                              {application.product.price}
                           </div> */}
                           <div class="text-right">
                              1234 5678 9652 8765
                           </div>
                        </div>

                        <div className="flex space-x-16">
                           <div>
                              <p className="text-sm text-primary-200 mb-1">Activated on</p>
                              {/* <p className="text-sm">{application.policy_details.active_on}</p> */}
                              <p className="text-sm">{moment(application.start_date).format('DD MMMM YYYY')}</p>
                           </div>
                           <div>
                              <p className="text-sm text-primary-200 mb-1">Valid till</p>
                              {/* <p className="text-sm">{application.policy_details.expire_on}</p> */}
                              <p className="text-sm">{moment(application.end_date).format('DD MMMM YYYY')}</p>
                           </div>
                        </div>
                     </div>

                     <AppHeading className="mt-10 mb-5">Covered Members</AppHeading>

                     <PanelGroup accordion bordered defaultExpanded={false}>
                        {application.members.map(member => {
                           return <ExpandableComponent member={member} policyId={application.policy_id} />
                        })}
                     </PanelGroup>

                  </>}
               </div>
               <div className="flex-1">
                  <div className="lg:ml-auto lg:mr-0 mx-auto w-80 h-96 flex flex-col">
                     <div className="p-4 rounded-xl shadow">
                        {/* <p className="font-semibold mb-3">Update KYC</p> */}
                        <p className="text-xs mb-3">Your KYC is pending, Please update your KYC.</p>
                        <Button attrs={{ onClick: () => this.props.history.push(`/update-kyc/${application.id}`) }} outline={true} title="Update KYC">
                           <UploadIcon width={22} className="mr-2" />
                        </Button>
                     </div>
                  </div>
               </div>
            </div>

         </Main >
      )
   }

   async componentDidMount() {
      const id = this.props.match.params.appId;
      await this.getApplication(id)
   }

   getApplication = async (id) => {
      AppStore.dispatch({ type: 'LOADING', loading: true })
      const response = await ApiGet(`subscription/${id}`);
      if (response.status === 'success') {
         this.setState({ application: response.data })
      } else {
         await setError(response.message);
         this.props.history.push("/");
      }
      AppStore.dispatch({ type: 'LOADING', loading: false })
   }

   onError = (error) => {
      setError(error, () => {
         this.props.history.replace("/subscriptions")
      })
   }

   onSuccess = async (message) => {
      await this.getApplication(this.state.application.id);
      this.MessageRef.current?.show(message, "success");
   }
}

const mapStateToProps = (state) => {
   const { loading } = state
   return { loading: loading }
};

const ApplicationConnect = connect(mapStateToProps)(props => {
   return <Application {...props} />
})

export default withRouter(ApplicationConnect)