import { Avatar, Checkbox, Radio, Stack } from "@mui/material";
import React, { useState } from "react";

export const UserCard = ({
  valueField,
  labelField,
  handleRadioChange,
  selectedValue,
  item = {},
}) => {
  const controlProps = (value) =>
    console.log(
      "@@@@valuefield:::",
      valueField,
      item,
      item[valueField],
      selectedValue,
      value
    ) || {
      checked: selectedValue.indexOf(value) > -1,
      onChange: handleRadioChange,
      value: value,
      name: "size-radio-button-demo",
      inputProps: { "aria-label": value },
    };
  return (
    <Stack
      style={{
        boxShadow: "1px 0px 15px -1px rgba(0,0,0,0.56)",
        borderRadius: 10,
        flexDirection: "row",
        display: "flex",
        padding: 20,
        alignItems: "center",
        justifyContent: "space-between",
        margin: 20,
        cursor: "pointer",
      }}
      onClick={() => {
        handleRadioChange({ target: { value: Number(item[valueField]) } });
      }}
    >
      <Stack direction="row" alignItems={"center"} spacing={2}>
        <Avatar sx={{ width: 40, height: 40 }}>{item[labelField][0]}</Avatar>
        <div>{item[labelField]}</div>
      </Stack>
      <Checkbox
        {...controlProps(item[valueField])}
        sx={{
          "& .MuiSvgIcon-root": {
            fontSize: 30,
          },
        }}
      />
    </Stack>
  );
};
