import React from 'react'
import { connect } from 'react-redux'
import InputLabel from './InputLabel';

export function InputRadio({ loading = false, name, label = "", labelLink = null, attrs = {}, options = [], value = null, inline = true, className = "", error = null }) {

  let loading_disabled = loading ? { disabled: true } : {};

  return (
    <div className="mb-5">
      <InputLabel label={label} labelLink={labelLink} />
      <div className={[`mt-2 flex ${inline ? '' : 'flex-col'} ${inline ? 'space-x-4' : 'space-y-4'}`, className].join(" ")}>
        {options.map((option, index) => {
          return <div key={index} className={[`flex items-center`, inline ? '' : 'flex-row-reverse space-x-reverse space-x-4 justify-end'].join(' ')}>
            <input
              id={`${name}-${index}-${option.value}`}
              name={`${name}`}
              type="radio"
              className="focus:ring-primary-500 h-5 w-5 text-primary-500 border-gray-300"
              defaultChecked={value === option.value}
              value={option.value}
              {...attrs}
              {...loading_disabled}
            />
            <label htmlFor={`${name}-${index}-${option.value}`} className={`ml-2 block text-sm text-gray-700${inline ? '' : ' flex-1'}`}>
              {option.label}
            </label>
          </div>
        })}
      </div>
      {error ? <p className="mt-2 text-xs text-red-500">{error.join(' ')}</p> : false}
    </div>
  )
}

const mapStateToProps = (state) => {
  const { loading } = state
  return { loading: loading }
};

export default connect(mapStateToProps)(props => {
  return <InputRadio {...props} />
})